.nmPageContainer
{
	min-height: 100%;
	height:100%;
	position: relative;
    /*overflow-y: hidden;*/
    overflow-x: hidden;
}
.nmPageHeader
{
	position: relative;
    max-height:100px;
}
.nmPageHeader .nmLogo 
{
    -webkit-transition: width 0.3s ease-in-out;
    -o-transition: width 0.3s ease-in-out;
    transition: width 0.3s ease-in-out;
    display: block;
    float: left;
    height: 50px;
    font-size: 20px;
    line-height: 50px;
    text-align: center;
    width: 250px;
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    padding: 0 15px;
    font-weight: 300;
    overflow: hidden;
    outline: none;
    text-decoration: none;
}
@media (min-width: 768px)
{
	.nsNavCollapsed .nmPageHeader .nmLogo 
	{
	    width: 50px;
	}
	.nsNavCollapsed .nmPageHeader .nmLogo > .nmLogoTextMini 
	{
	    display: block;
	    margin-left: -15px;
	    margin-right: -15px;
	    font-size: 18px;
	}
	.nsNavCollapsed .nmPageHeader .nmLogo > .nmLogoText 
	{
	    display: none;
	}
	.nsNavCollapsed .nmPageHeader .nmPageHeaderContent 
	{
    	margin-left: 50px;
	}
}
@media (max-width: 767px) 
{
	  .nmPageHeader 
	  {
	    position: relative;
	  }
	  .nmPageHeader .nmLogo,
	  .nmPageHeader .nmPageHeaderContent 
	  {
	    width: 100%;
	    float: none;
	    position: relative!important;
	  }
	  .nmPageHeader .nmPageHeaderContent 
	  {
	    	margin: 0;
	    	margin-left: 0px!important;
	  }
	  .nmPageHeader .navbar-custom-menu {
	    float: right;
	  }
}
.nmPageHeader .nmLogo .nmLogoTextMini 
{
    display: none;
}
.nmPageHeader .nmPageHeaderContent
{
	-webkit-transition: margin-left 0.3s ease-in-out;
    -o-transition: margin-left 0.3s ease-in-out;
    transition: margin-left 0.3s ease-in-out;
    margin-bottom: 0;
    margin-left: 250px;
    border: none;
    min-height: 50px;
    border-radius: 0;
}
.nmHeaderRight>.nmAppName
{
	padding: 11px 15px 8px 10px;
	position: relative;
    float: left;
    background-color: transparent;
    background-image: none;
    overflow: hidden;
}
.nmHeaderRight>.nmAppName > h1 
{
    color: #fff;
    margin: 0;
    font-size: 24px;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 100%;
}
.nmHeaderRight>.nmAppDetail 
{
    position: relative;
    float: right;
    padding-right: 10px;
}
.nmHeaderRight>.nmAppDetail>.welcomeMessage 
{
    float: left;
    position: relative;
}
.nmHeaderRight>.nmAppDetail>.welcomeMessage>.envInfo 
{
    float: right;
    padding: 0 3px;
}
.nmHeaderRight 
{
    border: medium none;
    height: 50px;
    margin: 0;
    min-width: 960px;
    padding: 0;
    width: 100%;
}
.nmPageHeader .appName 
{
    position: relative;
    float: left;
    background-color: transparent;
    background-image: none;
    padding: 8px 15px 8px 120px;
    overflow: hidden;
}
.nmPageHeader .appName > h1 
{
    color: #fff;
    margin: 0;
