.table-container.table-data-with-Wordwrap th, 
.table-container.table-data-with-Wordwrap td {
	word-wrap: break-word;
	word-break: break-all;
	line-break: strict;
	white-space: normal;
}
/*You need to set min-width and max-width for columns*/
.table-container.table-data-with-ellipsis td {
	white-space: nowrap; 
	overflow: hidden;
    text-overflow: ellipsis;
}
.table-container.table-data-with-ellipsis th{
	word-wrap: break-word;
	line-break: strict;
	white-space: normal;
}
/*Scrollable Table - End*/
/*Auto complete end*/
.content-header {
  background: transparent;
  position: relative;
  padding: 10px 15px 10px 15px;
}
.content-header > h1 {
  margin: 0;
  font-size: 20px;
}
.content-header > h1 > small {
  font-size: 15px;
  display: inline-block;
  padding-left: 4px;
  font-weight: 300;
}
.content-header > .breadcrumb {
  float: right;
  background: transparent;
  margin-top: 0px;
  margin-bottom: 0;
  font-size: 12px;
  padding: 7px 5px;
  position: absolute;
  top: 15px;
  right: 10px;
  border-radius: 2px;
}
.content-header > .breadcrumb > li > a {
  color: #444;
  text-decoration: none;
  display: inline-block;
}
.content-header > .breadcrumb > li > a > .fa,
.content-header > .breadcrumb > li > a > .glyphicon,
.content-header > .breadcrumb > li > a > .ion {
  margin-right: 5px;
}
.content-header > .breadcrumb > li + li:before {
  content: '>\00a0';
}
@media (max-width: 991px) {
  .content-header > .breadcrumb {
    position: relative;
    margin-top: 5px;
    top: 0;
    right: 0;
    float: none;
    background: #d2d6de;
    padding-left: 10px;
  }
  .content-header > .breadcrumb li:before {
    color: #97a0b3;
  }
}

